.header{position:fixed;top:30px;left:0;right:0;z-index:100;display:flex;justify-content:center}.header__inner{display:flex;justify-content:space-between;align-items:center;max-width:1530px;width:100%;height:40px}.header .logo{font-size:1.6rem;font-weight:bold;color:#111}.header .logo::before{content:"✘";color:#f60;margin-right:8px}.header .nav__list{display:flex;gap:30px;list-style:none}.header .nav__list a{color:#111;text-decoration:none;font-weight:500;transition:color .2s ease}.header .nav__list a.active,.header .nav__list a:hover{color:#f60}.header .header__right{display:flex;align-items:center;gap:16px}.header .header__right .talk-btn{background:#111;color:#fff;border:none;padding:10px 18px;border-radius:20px;font-size:.9rem;cursor:pointer}.header .header__right .talk-btn:hover{background:#f60}.header .header__right .hamburger{width:20px;height:16px;display:flex;flex-direction:column;justify-content:space-between;cursor:pointer}.header .header__right .hamburger span{height:2px;background:#111;border-radius:2px}